export default class MaternityCalcHelper {
  public static totalMaternityAllowanceFee(
    standardMonthlyRemuneration: number,
    startDateOfMaternityLeave: Date,
    dateOfBirth: Date
  ): number {
    // 標準報酬日額=標準報酬月額の平均÷30日
    // 10円未満四捨五入
    const dailyRemuneration =
      Math.round(standardMonthlyRemuneration / 30 / 10) * 10;

    // 給付日額は2/3
    // 1円未満四捨五入
    const dailyBenefitAmount = Math.round((dailyRemuneration * 2) / 3);

    // 出産手当金
    return (
      dailyBenefitAmount *
      this.maternityLeavePeriod(startDateOfMaternityLeave, dateOfBirth)
    );
  }

  public static endDateOfMaternityLeave(
    startDateOfMaternityLeave: Date,
    dateOfBirth: Date
  ) {
    const endDateOfMaternityLeave = new Date(startDateOfMaternityLeave);
    endDateOfMaternityLeave.setDate(
      startDateOfMaternityLeave.getDate() +
        this.maternityLeavePeriod(startDateOfMaternityLeave, dateOfBirth) -
        1
    );
    return endDateOfMaternityLeave;
  }

  public static maternityLeavePeriod(
    startDateOfMaternityLeave: Date,
    dateOfBirth: Date
  ): number {
    const diffTime =
      dateOfBirth.getTime() - startDateOfMaternityLeave.getTime();
    const diffDay = Math.floor(diffTime / (1000 * 60 * 60 * 24));
    // 出産予定日を含めて42日間
    // 出産日翌日から56日間分
    // 予定日通りだと98日分
    return diffDay + 1 + 56;
  }
}
